<template>
    <div>
      <el-col :span="24">
        <el-form :inline="true" :model="formInline" class="demo-form-inline" size="small">
          <!-- <el-form-item label="缴费类型">
              <el-select v-model="formInline.value1" placeholder="请选择缴费类型" clearable>
                  <el-option label="体检缴费" value="体检缴费"></el-option>
                  <el-option label="照相缴费" value="照相缴费"></el-option>
                </el-select>
          </el-form-item> -->
          <el-form-item label="支付方式" v-if="modeClick">
              <el-select v-model="formInline.value2" placeholder="请选择支付方式" clearable>
                  <el-option label="微信" value="微信"></el-option>
                  <el-option label="支付宝" value="支付宝"></el-option>
                  <el-option label="现金" value="现金"></el-option>
                </el-select>
          </el-form-item>
          <el-form-item label="日期选择">
            <el-date-picker
                  v-model="formInline.value3"
                  type="daterange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期">
                </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="downList">下载统计表</el-button>
            <span style="margin: 10px 0 10px 20px;color: red;">注：默认统计本日数据，包含所有收费成功数据(设备收费净收入=总收入-退款数)。</span>
          </el-form-item>
        </el-form>
      </el-col>
      <el-card>
        <el-row><el-col :span="4" :push="20"><el-button @click="modeSwitch">{{modeDeom}}</el-button></el-col></el-row>
        <ve-histogram :settings="chartSettings" :data="chartData" v-if="modeClick"></ve-histogram>
         <el-table
            v-else
            :row-class-name="tableRowClassName"
            :data="chartData.rows"
            style="width: 100%">
            <el-table-column
              prop="Machine"
              label="设备编号"
              width="180">
            </el-table-column>
            <el-table-column
              prop="Alipay"
              label="支付宝收款"
              width="125">
            </el-table-column>
            <el-table-column
              prop="WeChat"
              label="微信收款"
              width="125">
            </el-table-column>
            <el-table-column
              prop="Cash"
              label="现金收款"
              width="125">
            </el-table-column>
            <el-table-column
              prop="Refund"
              label="退款金额"
              width="125">
            </el-table-column>
            <el-table-column
              prop="NetIncome"
              label="净收入"
              width="125">
            </el-table-column>
            <el-table-column
              prop="Total"
              label="总收入"
              width="125">
            </el-table-column>
          </el-table>
      </el-card>
    </div>
</template>
<script>
    const axios = require("axios");
    import VeHistogram from 'v-charts/lib/histogram'
  	export default {
      data(){
        return{
          chartSettings: {
            labelMap: {'NetIncome': '净收入', 'PersonTime': '人数'}
          },
          chartData:'',
          formInline:{
            // value1:'',
            value2:'',
            value3:''
          },
          modeDeom:'表格方式',
          modeClick:true
        }
      },
      methods:{
        tableRowClassName({row, rowIndex}) {
          if (rowIndex === 4) {
            return 'warning-row';
          }
          return '';
        },
        onSubmit(){
          // console.log('查询')
          this.$message({
            showClose: true,
            // message: res.data.Message,
            message: "该时间段有数据被篡改",
            type: "error"
        });
        },
        downList(){
          console.log('下载统计表')
        },
        //切换展示方式
        modeSwitch(){
          if(this.modeClick){
            this.modeClick = false
            this.modeDeom = '图表方式'
          }else{
            this.modeClick = true
            this.modeDeom = '表格方式'
          }
        }
      },
      created () {
          this.chartData = {
            columns: ["Machine","NetIncome","PersonTime"],
            rows: [
              { Machine: 'tjj001', Total: 1232, PersonTime:123,Alipay:342,WeChat:213,Cash:435,Refund:132,NetIncome:1120},
              { Machine: 'tjj002', Total: 1223 , PersonTime:232,Alipay:343,WeChat:243,Cash:424,Refund:134,NetIncome:1121},
              { Machine: 'tjj003', Total: 2123 , PersonTime:443,Alipay:344,WeChat:256,Cash:478,Refund:155,NetIncome:1122},
              { Machine: 'tjj004', Total: 4123 , PersonTime:231,Alipay:345,WeChat:276,Cash:499,Refund:166,NetIncome:1123},
              { Machine: 'tjj005', Total: 3123 , PersonTime:223,Alipay:346,WeChat:234,Cash:497,Refund:178,NetIncome:1124},
              { Machine: 'tjj006', Total: 1000 , PersonTime:121,Alipay:347,WeChat:266,Cash:467,Refund:144,NetIncome:1125},
              { Machine: '合计', Total: 6000 , PersonTime:1000,Alipay:1500,WeChat:1300,Cash:900,Refund:600,NetIncome:7000}
            ]
          }
        },
        components: { VeHistogram }
    }
</script>
<style scoped>
  .el-table .warning-row {
    background: oldlace;
  }
</style>
